<template>
	<div class="group-item">
		<img src="../assets/images/ic_rec_group_banner_2.png" alt="" class="titlePic" v-show="mark < 2">
		<div class="content">
			<span class="g-item-title">{{ titleName }}</span>
			<hr>
			<slot></slot>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["titleName","mark","url"]
	}
</script>

<style type="text/css">
	.group-item {padding: 20px 10px;}
	.group-item .titlePic {width: 100%;}
	.group-item .content {position: relative;}
	.group-item .g-item-title {padding: 5px 10px; position: absolute; left: 43%; top: -13px; background-color: #fff;}
	.group-item hr {margin: 20px 0;}

</style>


