<template>
	<div>
		<MyHeader txt="书影音">
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot="leftImg" class="leftImg">
			<img src="../../assets/images/ic_chat_green.png" slot="rightImg" class="rightImg">
		</MyHeader>
		<div class="nav">
			<span @click="change(1)"><router-link to="/audio/film" :class="{ac: mark == 1}">电影</router-link></span>
			<span @click="change(2)"><router-link to="/audio/book" :class="{ac: mark == 2}">读书</router-link></span>
			<span @click="change(3)"><router-link to="/audio/tv" :class="{ac: mark == 3}">电视</router-link></span>
			<span @click="change(4)"><router-link to="/audio/city" :class="{ac: mark == 4}">同城</router-link></span>
			<span @click="change(5)"><router-link to="/audio/music" :class="{ac: mark == 5}">音乐</router-link></span>
		</div>
