		<div class="show">
			<router-view></router-view>
		</div>
		<div class="fillBot"></div>
	</div>
</template>

<script type="text/javascript">
import MyHeader from "../../components/header"

	export default {
		data: function () {
			return {
				mark: 1
			}
		},
		methods: {
			change: function (num) {
				console.log(123)
				this.mark = num;
			}
		},				
		components: {
			MyHeader
		},
		computed: {
			bol: function () {

			}
		},


	}

</script>

<style type="text/css">
	.nav {position: fixed; top: 49px; left: 0; border-bottom: solid 1px #e4e4e4; height: 30px; line-height: 26px; background-color: #fff; width: 100%; z-index: 999;}
	.nav span {width: 20%; display: block; float: left;}
	.nav a {display: block; text-decoration: none; color: #898989; border-bottom: solid 4px #fff; height: 26px; font-size: 14px;}
	/*.nav a.router-link-active {border-bottom-color: #64bb74; color: #64bb74}*/
	.nav .ac {border-bottom-color: #64bb74; color: #64bb74}
	.fill {height: 90px; background-color: #f7f7f7;}
</style>


