<template>
	<div>
		<h1>{{ msg }}</h1>
		<Child :change="changeMsg"></Child>
	</div>
</template>

<script type="text/javascript">
	import Child from './Child'

	export default({
		components: {
			Child
		},
		data: function () {
			return {
				msg: "Update your date Page"
			}
		},
		methods: {
			changeMsg: function (num) {
				this.msg = "Update your date Page: " + num;
			}
		}
	});
</script>

<style type="text/css">
	
</style>




