<template>
  <div id="app">
    <router-view></router-view>
    <tarbar></tarbar>
  </div>
</template>

<script>
import Tarbar from "./components/tarbar"

export default {
  name: 'app',
  created: function () {
    this.$router.push("/")
  },
  components: {
    Tarbar
  }
}
</script>

<style>
* {margin: 0; padding: 0;}
a {text-decoration: none; color: #666}
.clear {clear: both;}
.fillBot {height: 65px;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



