<template>
	<div class="audioNews">
		<a href="">
			<img :src="url" alt="">
			<p>{{ des }}</p>
		</a>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["url", "des"]
	}
</script>

<style type="text/css">

.audioNews {width: 100%; border-bottom: solid 15px #f7f7f7;}
.audioNews a {display: block;}
.audioNews a img {width: 100%}
.audioNews a p {padding: 20px;}
</style>


