<template>
	<div class="itemWrap" @click="fn">
		<span v-show="bol"><slot name="normalImg"></slot><br></span>
		<span v-show="!bol"><slot name="activeImg"></slot><br></span>
		<span v-text="txt" :class="{ac: !bol}"></span>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["txt","id"],
		computed: {
			bol: function () {
				if (this.$parent.sel == this.id) {
					return false;
				}
				return true;
			}
		},
		methods: {
			fn: function () {
				this.$router.push("/" + this.id);
				this.$emit("change", this.id)
			}
		}		
	}

</script>

<style type="text/css">
	.itemWrap {width: 20%; float: left; margin-top: 5px;}
	.itemWrap span {font-size: 12px;}
	.itemWrap img {width: 30px;}
	.itemWrap .ac {color: #64bb74;}
</style>


