<template>
	<div class="home-content">
		<h3><span></span>{{ title }}</h3>
		<slot></slot>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["title"]
	}
</script>

<style>
	.home-content {text-align: left; }
	.home-content h3 {color: #fc8243; font-size: 14px; padding-top: 15px; padding-left: 20px; font-weight: normal;}
</style>


