<template>
	<div class="broadcast-item">
		<a class="close" @click="delItem(mark)">X</a>
		<a class="guanzhu">关注</a>
		<a class="touxiang"><img src="../assets/images/ic_user_info_gender.png" alt=""></a>
		<div class="b-item-wrap">
			<h4>{{ name }}</h4>
			<p class="people"><span>{{ num }}</span>人关注</p>
			<p class="text">{{ text1 }}</p>
			<div class="textCon">
				<p>{{ text2 }}</p>
			</div>
			<p class="vote"><span class="like"><img src="../assets/images/ic_like_full.png" alt="" @click="vote(mark)">{{ like }}</span><span class="comment"><img src="../assets/images/ic_comment_full.png" alt="">{{ comment }}</span></p>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["name","num","text1","text2","like","comment", "mark"],
		methods: {
			delItem: function (i) {
				this.$parent.arrPerson.splice(i, 1);
			},
			vote: function (i) {
				this.$parent.arrPerson[i].like++;
			}
		}
	}
</script>

<style type="text/css">
	.broadcast-item {background-color: #fff; border-radius: 8px; padding: 25px 25px 25px 80px; margin-bottom: 15px; position: relative;}
	.close {position: absolute; font-size: 16px; top: 10px; right: 10px;}
	.guanzhu {position: absolute; right: 25px; top: 30px; display: block; background: #42c055; color: #fff; text-align: center; padding: 10px 30px;border-radius: 5px;}
	.touxiang {position: absolute; top: 25px; left: 25px; display: block;}
	.touxiang img {width: 50px;}

	.broadcast-item .b-item-wrap {text-align: left;}
	.broadcast-item .b-item-wrap h4 {}
	.broadcast-item .b-item-wrap p {padding: 10px 0;}
	.broadcast-item .b-item-wrap .people {font-size: 14px; color: #999;}
	.broadcast-item .b-item-wrap .text {}
	.broadcast-item .b-item-wrap .textCon {padding: 0 10px; background-color: #161b2f; height: 150px; overflow: hidden; margin-bottom: 10px;}
	.broadcast-item .b-item-wrap .textCon p {line-height: 25px; color: #fff; }
	.b-item-wrap .vote {width: 100px; height: 20px; line-height: 20px;}
	.b-item-wrap .vote span {position: relative; padding-left: 25px;}
	.b-item-wrap .vote img {width: 20px; position: absolute;left: 0}
	.b-item-wrap .vote .like {float: left;}
	.b-item-wrap .vote .comment {float: right;}
</style>


