<template>
	<div class="headerWrap" :style="{'background-color': col}">
		<span v-text="txt"></span>
		<slot name="leftImg"></slot>
		<slot name="rightImg"></slot>
		<slot></slot>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: {
			col: {
				default: "#f7f7f7"
			},
			txt: {
				default: ""
			}
		},
	}
</script>

<style type="text/css">
	.headerWrap {width: 100%; height: 48px; position: fixed; top: 0; left: 0; border-bottom: solid 1px #b9b9b9; z-index: 999;}
	.headerWrap span {font-size: 16px; color: #666; line-height: 48px;}
	.leftImg {width: 25px; position: absolute; right: 70px; top: 10px;}
	.rightImg {width: 25px; right: 20px; top: 10px; position: absolute;}

</style>


