<template>
	<div>
		<MyHeader txt="小组">
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot="leftImg" class="leftImg">
			<img src="../../assets/images/ic_chat_green.png" slot="rightImg" class="rightImg">		
		</MyHeader>
		<div class="groupFill"></div>
		<div class="group-header">
			<h3>{{ groupNum }}个小组等你来玩</h3>
			<h4>立即选择加入吧!</h4>
			<p>根据你的兴趣推荐</p>
		</div>
		<GroupItem :titleName="item.name" :mark="item.index" :url="item.url" v-for="item in arrGroup">
			<GroupItemCon :name="item.name" :text="item.text" :num="item.num" v-for="item in arrGroupList"></GroupItemCon>
		</GroupItem>
		<Choose text="选好了,去玩小组"></Choose>
		<div class="fillBot"></div>
	</div>
</template>

<script type="text/javascript">
import MyHeader from "../../components/header"
import GroupItem from "../../components/group-item"
import GroupItemCon from "../../components/group-item-con"
import Choose from "../../components/choose"

	export default {
		components: {
			MyHeader,GroupItem,GroupItemCon,Choose
		},
		data: function () {
			return {
				groupNum: 418895,
				arrGroup: [
					{name: "影视", url: "../assets/images/ic_rec_group_banner_2.png", index: 0},
					{name: "科技", url: "../assets/images/ic_rec_group_banner_2.png", index: 1},
					{name: "电视剧", url: "../assets/images/ic_rec_group_banner_2.png", index: 2},
				],
				arrGroupList: [
					{name: "每日推荐", text: "川普、示威游行，以及运作中的割裂世界的心理战", num: "253612"},
					{name: "每日推荐", text: "川普、示威游行，以及运作中的割裂世界的心理战", num: "253612"},
					{name: "每日推荐", text: "川普、示威游行，以及运作中的割裂世界的心理战", num: "253612"},
				]
			}
		}
	}

</script>

<style type="text/css">
	.groupFill {height: 49px;}
	.group-header {background: url(../../assets/images/ic_group_banner.png) no-repeat 0 0; background-size: cover;}
	.group-header h3 {padding-top: 60px; color: #444;}
	.group-header h4 {padding-bottom: 60px; color: #a3a3a3; font-weight: normal;}
	.group-header p {text-align: left; padding-left: 10px; color: #a3a3a3; font-size: 12px;}
</style>


