<template>
	<div class="choose">
		<div class="chooseWrap">
			<div class="changeList">
				<img src="../assets/images/ic_reshare_flag.png" alt="">
				<span>换一批</span>
			</div>
			<div class="chooseOk">
				<span>{{ text }}</span>
			</div>
		</div>	
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["text"]
	}
</script>

<style>
	.choose {position: fixed; bottom: 65px; left: 0; height: 64px; background-color: #fff; width: 100%}
	.chooseWrap {padding: 10px 15px; height: 44px;}
	.choose .changeList {position: absolute; left: 15px; top: 25px;}
	.choose .changeList img {width: 20px; float: left; margin-right: 5px;}
	.choose .changeList span {font-size: 12px; color: #47b95a; float: right;}	
	.choose .chooseOk {position: absolute; right: 15px; top: 20px; text-align: center; color: #fff; padding: 5px 10px; background-color: #e2e2e2; border-radius: 3px; font-size: 12px;}
</style>


