<template>
	<div class="swiper-container" :class="swiperid">
		<div class="swiper-wrapper">
			<slot></slot>
		</div>
		<div :class="{'swiper-pagination': pagination}" :style="{'text-align': paginationDiretion}"></div>
<!-- 		<div class="swiper-button-prev swiper-button-white"></div>
		<div class="swiper-button-next"></div> -->
	</div>
</template>

<script type="text/javascript">
import "../assets/libs/swiper/js/swiper.js"

	export default {
		props: {
			swiperid: {
				type: String,
				default: ""
			},
			pagination: {
				type: Boolean,
				default: true
			},
			paginationDiretion: {
				type: String,
				default: "center"
			},
			effect: {
				type: String,
				default: "slide"
			},
			loop: {
				type: Boolean,
				default: true
			},
			direction: {
				type: String,
				default: "horizontal"
			},
			autoplay: {
				type: Number,
				default: 2000
			},
			paginationType: {
				type: String,
				default: "bullets"
			},									
		},
		mounted: function () {
		var That = this;
			new Swiper("." + That.swiperid, {
				loop: That.loop,
				autoplay: That.autoplay,
				pagination: ".swiper-pagination",
				effect: That.effect,
				direction: That.direction,
				paginationType: That.paginationType,
				// prevButton: ".swiper-button-prev",
				// nextButton: ".swiper-button-next"
			})
		}		
	};


</script>

<style type="text/css">
@import "../assets/libs/swiper/css/swiper.css";

.swiper-container img {width: 100%;}
.swiper-pagination-bullet-active {background: #fff}

/*.swiper-button-next {background-size: 13px 22px;}*/
</style>


