<template>
	<div class="item" :style="{'left': left}">
		<img :src="url">
		<div class="name"><a href="">{{ name }}</a></div>
		<div class="star">
			<!-- <img src="../assets/images/rating_star_card_on.png" alt="">
			<img src="../assets/images/rating_star_card_on.png" alt="">
			<img src="../assets/images/rating_star_card_on.png" alt="">
			<img src="../assets/images/rating_star_card_on.png" alt="">
			<img src="../assets/images/rating_star_card_empty.png" alt="">
			<span>{{ score }}</span> -->
			<slot></slot>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["url", "name", "score", "left"]
	}
</script>

<style type="text/css">

.audioPic .item {width: 25%; margin-right: 10px; float: left; position: absolute; top: 0}
.audioPic .item > img {width: 100%;}
.audioPic .item .name {padding: 5px 0 ;}
.audioPic .item a {text-decoration: none; color: #333; font-size: 12px;}
.audioPic .item .star {vertical-align: middle; height: 20px;}
.audioPic .item .star img {width: 12px; display: inline-block; float: left; vertical-align: middle;}
.audioPic .item span {color: #999; font-size: 12px;}
</style>


