<template>
	<div>
		<ul>
			<li :class="currentPage == 1 ? 'dis' : ''"><a href="##" @click="turnToPage(1)">首页</a></li>
			<li :class="currentPage == 1 ? 'dis' : ''"><a href="##" @click="turnToPage(currentPage-1)">上一页</a></li>
			<li v-if="currentPage-3 > 0" @click="turnToPage(currentPage-3)"><a href="##">{{ currentPage-3 }}</a></li>
			<li v-if="currentPage-2 > 0" @click="turnToPage(currentPage-2)"><a href="##">{{ currentPage-2 }}</a></li>
			<li v-if="currentPage-1 > 0" @click="turnToPage(currentPage-1)"><a href="##">{{ currentPage-1 }}</a></li>
			<li class="ac"><a href="##">{{ currentPage }}</a></li>
			<li v-if="currentPage+1 < totalPage+1" @click="turnToPage(currentPage+1)"><a href="##">{{ currentPage+1 }}</a></li>
			<li v-if="currentPage+2 < totalPage+1" @click="turnToPage(currentPage+2)"><a href="##">{{ currentPage+2 }}</a></li>
			<li v-if="currentPage+3 < totalPage+1" @click="turnToPage(currentPage+3)"><a href="##">{{ currentPage+3 }}</a></li>
			<li :class="currentPage == totalPage ? 'dis' : ''"><a href="##" @click="turnToPage(currentPage+1)">下一页</a></li>
			<li :class="currentPage == totalPage ? 'dis' : ''"><a href="##" @click="turnToPage(totalPage)">尾页</a></li>
		</ul>
		<small>当前页面为: <span>{{ currentPage }}页</span> 总计:<span>{{ totalPage }}页</span></small>
		<input type="number" v-model="goToPage" :class="{err: bol}"></input>
		<button @click="turnToPage(goToPage)">go</button>
	</div>
</template>

<script type="text/javascript">
	export default({
		data: function () {
			return {
				currentPage: 50,
				totalPage: 100,
				goToPage: "",
				bol: false
			}
		},
		props: ["change"],
		methods: {
			turnToPage: function (num) {
				num = parseInt(num)
				if (!isNaN(num) && (num > 0 && num <= this.totalPage)) {
					this.bol = false;
					this.goToPage = "";
					this.currentPage = num;
					this.change(num);
				} else {
					this.bol = true;
				}
			}
		}
	})
</script>

<style type="text/css">
	a {text-decoration: none; color: #666;}
	ul {display: inline-block; margin-bottom: 0}
	li {list-style: none; float: left; padding: 5px 10px; border: solid 1px #ccc;}
	small span {font-size: 20px; font-style: italic; color: orange}
	.ac {background-color: #eee;}
	.dis a {cursor: not-allowed}
	.err {border: solid 1px red;}
</style>


