<template>
	<div class="home-item">
		<h4 v-text="head"></h4>
		<p class="des">{{ des }}</p>
		<p class="author"><span class="au">作者: {{ author }}</span></p>
		<img :src="url" alt="" class="item-pic">
	</div>
</template>

<script type="text/javascript">
	export default {
		props: ["head","des","author","url"]
	}
</script>

<style>
.home-item {padding: 20px; border-bottom: solid 1px #e4e4e4; position: relative;}
.home-item h4 {width: 60%; color: #444; font-size: 16px; padding-bottom: 10px;}
.home-item .des {width: 60%; font-size: 12px; color: #b9b9b9; margin-bottom: 15px; height: 35px; overflow: hidden;}
.home-item .author {font-size: 12px; color: #999;}
.home-item .item-pic {position: absolute; top: 20px; right: 20px; width: 100px;}
</style>


