<template>
	<div class="book">
		<Hot title="新书速递">
			<AudioItem v-for="(item,index) in arrPic" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>
		<Hot title="最受关注的虚构类图书">
			<AudioItem v-for="(item,index) in arrPic2" :url="item.url" :name="item.name" :score="item.score" :left="index * 28 + '%'">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
				<span>{{ item.score }}</span>
			</AudioItem>
		</Hot>		
		<AudioNews :url="url" :des="des"></AudioNews>
	</div>
</template>

<script type="text/javascript">
import Hot from './audio-hot'
import AudioNews from './audio-news'
import AudioItem from './audio-item'

	export default {
		components: {
			Hot,AudioNews,AudioItem
		},
		data: function () {
			return {
				url: require("../assets/images/bookNews.jpg"),
				des: "豆瓣书店电影图书福利月",
				arrPic: [
					{url: require("../assets/images/book1.jpg"), name: "你好，法奈利", score: "7.5"},
					{url: require("../assets/images/book2.jpg"), name: "世间一切", score: "7.6"},
					{url: require("../assets/images/book3.jpg"), name: "创新者", score: "9.2"},
					{url: require("../assets/images/book4.jpg"), name: "掌故", score: "6.6"},
				],
				arrPic2: [
					{url: require("../assets/images/book5.jpg"), name: "日日之食", score: "7.5"},
					{url: require("../assets/images/book6.jpg"), name: "四月间事", score: "7.6"},
					{url: require("../assets/images/book7.jpg"), name: "在世界与我", score: "9.2"},
					{url: require("../assets/images/book8.jpg"), name: "艰难的制造", score: "6.6"},
				],								
			}
		}
	}	
</script>

<style type="text/css">
.book p {text-align: left;}

.audioPic .item > img {width: 100%; height: 125px;}
</style>


