				<span class="authorName">{{ item.authorName }}</span>
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_on.png" alt="">
				<img src="../assets/images/rating_star_card_empty.png" alt="">
