			return {
				url: require("../assets/images/musicNews.jpg"),
				des: "52赫兹,我爱你 同名主题曲",
				arrPic: [
					{url: require("../assets/images/music1.jpg"), name: "猎户星座", score: "7.5", authorName: "朴树"},
					{url: require("../assets/images/music2.jpg"), name: "北京女孩", score: "7.6", authorName: "张蔷"},
					{url: require("../assets/images/music3.jpg"), name: "放 & 披风", score: "9.2", authorName: "陈奕迅"},
					{url: require("../assets/images/music4.jpg"), name: "李志 电音", score: "6.6", authorName: "李志"},
				],
				arrPic2: [
					{url: require("../assets/images/music5.jpg"), name: "Witness", score: "7.5", authorName: "Katy Perry"},
					{url: require("../assets/images/music6.jpg"), name: "After Sex", score: "7.6", authorName: "Katy Perry"},
					{url: require("../assets/images/music7.jpg"), name: "Relaxer", score: "9.2", authorName: "Katy Perry"},
					{url: require("../assets/images/music8.jpg"), name: "hopeless", score: "6.6", authorName: "Katy Perry"},
				],								
			}
		}
	}	
</script>

<style type="text/css">
.music .audioPic {text-align: left; height: 150px;}
.music .audioPic .name {padding: 0;}
.music .audioPic .item > img {width: 100%; height: auto;}
.music .authorName {display: block; padding-bottom: 3px;}
</style>


